router.setDefaultPage(newsFeedView);

router.addRoutePath('./page', newsFeedView);
router.addRoutePath('./page', newsDetailView);
//ìµœì´ˆ ì§„ì…ì‹œ ë¼ìš°í„° í•¨ìˆ˜ ì§ì ‘ ì‹¤í–‰(ì²˜ìŒì—” í˜¼ì ì‘ë™í•˜ì§€ X)
router.route();
